import React, {useEffect, useRef} from 'react';
import './NewsPage.scss'
import Logo from "../../svg/Logo";
import EducationList from "../../components/EducationList/EducationList";
import Nav from "../../components/Nav/Nav";
import {script} from './../utils/script'
import {LocomotiveScrollProvider, useLocomotiveScroll} from "react-locomotive-scroll";
const NewsPage = () => {
    const containerRef = useRef(null)
    const { scroll } = useLocomotiveScroll()
    console.log(scroll);
    useEffect(()=>{
       setTimeout(()=>{},500)
       script()
   },[])
    return (
        <>
            <LocomotiveScrollProvider
                options={
                    {
                        smooth: true,
                        multiplier: 0.5,
                        direction: "vertical",
                        reloadOnContextChange: false,

                        tablet: {
                            breakpoint: 1024,
                            direction: "vertical",
                        },
                        smartphone: {
                            smooth: false
                        },
                        resetNativeScroll: false,
                    }
                }
                watch={
                    []
                }
                containerRef={containerRef}
            >
                <Nav cssClass={"navigation navigation-black"}/>
        <section className="educationPage" data-scroll-container  ref={containerRef}>

            <div className="educationPage__logo">
                <Logo color={"#373737"} cssClass ={"educationPage__logo__svg"}/>
                <h3 className={'title title--educationPage'} data-scroll-speed={5}>News</h3>
            </div>
            <ul className="education__content">
                <EducationList img={'./img/imgCity.jpg'} title={'     PROTG GX'}
                                description={' Integrated Control Technology       Protg GX 4.3.307.         .'}
                                link={'/news/description-full'} linkText={''} educationDate={'Mar 24, 2020'} linkCss ={'education__list__href__decoration'}
                />
                <EducationList img={'./img/news_train.jpg'} title={'  - 802.1X  IP- CIAS'}
                               description={'  802.1          -.            .  ,    -            802.1:'}
                               link={'/news/news2/description-full'} linkText={''} educationDate={'Mar 24, 2020'} linkCss ={'education__list__href__decoration'}
                />
                <EducationList img={'./img/imgCity.jpg'} title={'   AVIGILON H5A DUAL HEAD'}
                               description={'Avigilon H5A Dual Head       ,       -          : ,  ,  ,     .   -                  .'}
                               link={'/news/news3/description-full'} linkText={''} educationDate={'Mar 24, 2020'} linkCss ={'education__list__href__decoration'}
                />
            </ul>
            {/*<div className="educationPage__href" data-scroll-target={"nav"}>*/}
            {/*    <Link className = {'educationPage__href__decoration'} to={'/news/first-page'}>1</Link>*/}
            {/*    <Link className = {'educationPage__href__decoration educationPage__href__decoration--active'} to={'/news'}>2</Link>*/}
            {/*    <Link className = {'educationPage__href__decoration'} to={'/news/third-page'}>3</Link>*/}
            {/*</div>*/}
            {/*<Contact cssClass={'section-contact-wrapper'}/>*/}
        </section>
            </LocomotiveScrollProvider>
        </>
    );
};

export default NewsPage;